<script>

  const UNITREG = /^[\.\d]+(rem|px|%|em)$/;
  const DEFAULTSIZE = "30px";
  const DEFAULTCOLOR = "#999";

  /** getStyle;
   * css
   * @param el   
   * @param attr  css
   * @returns {* css}
   */
  const getStyle = function () {
    if (window.getComputedStyle) {
      return function (el, attr) {
        return window.getComputedStyle(el, null)[attr]
      }
    } else {
      return function (el, attr) {
        return el.currentStyle[attr]
      }
    }
  }();
  //
  //  const getStyle = function () {
  //    let isW3C = false;
  //    if (window.getComputedStyle) {
  //      isW3C = true;
  //    }
  //    return function (el, attr) {
  //      if (isW3C) {
  //        return window.getComputedStyle(el, null)[attr]
  //      } else {
  //        return el.currentStyle[attr]
  //      }
  //    }
  //  }();

  export default {
    data() {
      return {
        parentNodeColor: ""
      }
    },
    props: {
      "size": {
        type: [Number, String],
        default: 30
      },
      "color": {
        type: String,
      },
    },
    computed: {
      iconColor() {
        // color || color || ();
        let color = this.color || this.$parent.color || this.parentNodeColor;
        if (!color) {
          color = DEFAULTCOLOR;
          this.$nextTick(() => {
            let parentNodeColor = getStyle(this.$el.parentNode, 'color');
            // 
            if (parentNodeColor !== "rgb(0, 0, 0)") {
              this.parentNodeColor = parentNodeColor;
            }
          })
        }
        return color
      },
      iconSize() {
        let size = this.size;
        //  "0" (0) 
        if (!size || !parseInt(size)) {
          return DEFAULTSIZE;
        }
        //  rem / % / px
        if (typeof size === 'string' && size.search(UNITREG) !== -1) {
          return size;
        } else {
          // px
          return `${parseFloat(size)}px`;
        }
      }
    },
  }

</script>




